<template>
  <div>
      <!-- <div class="row">
<form class="col s12">
  <div class="row" v-for="(input,index) in formCardOption.formOption">
    <div class="input-field col s4">
      <input v-bind:type="input.type" class="validate" v-bind:placeholder="input.label" v-model="formCardOption.formModels[index].model">
    </div>
  </div>
  <a class="waves-effect waves-light btn-large" v-on:click='send'>Button</a>
</form>
</div> -->
<h2>Create your project</h2>
        <div id="footParts">
            <img src="https://carinapp.herokuapp.com/public/img/footParts.svg" height="400px">
                <div class="form">
                        
                    <h3>Parts sizes</h3>
                    <br>
                    <h5>Part 1</h5>
                    <p class="range-field">
                        <input type="range" v-model="partsSizes[0]" id="test5" min="0" max="20" step="0.1"/>
                    </p>
                    <h5>Part 2</h5>
                    <p class="range-field">
                        <input type="range"  v-model="partsSizes[1]" id="test5" min="0" max="20" step="0.1"/>
                    </p>
                    <h5>Part 3</h5>
                    <p class="range-field">
                        <input type="range"  v-model="partsSizes[2]" id="test5" min="0" max="20" step="0.1"/>
                    </p>
                </div>
        </div>
        <div id="motor">
                <div class="form" >
                        
                    <h3>Motor data sheet</h3>
                    <br><br>
                    <h5>Motor torque</h5>
                    <p class="range-field">
                        <input type="range" id="test5" min="0" max="20" step="0.1"/>
                    </p>
                    <h5>Motor speed</h5>
                    <p class="range-field">
                        <input type="range" id="test5" min="0" max="20" step="0.1"/>
                    </p>
                </div>
                <img src="https://carinapp.herokuapp.com/public/img/motor.svg" height="400px">

        </div>
         <div id="stream">
                <div class="form" >
                        
                    <h3>Live stream configuration</h3>
                    <br><br>
                    <h5>Number of parts</h5>
                    <p class="range-field">
                        <input type="range" id="test5" min="0" max="20" step="0.1"/>
                    </p>
                    <h5>Image quality</h5>
                    <p class="range-field">
                        <input type="range" id="test5" min="0" max="20" step="0.1"/>
                    </p>
                </div>
                <img src="https://carinapp.herokuapp.com/public/img/stream.svg" height="400px">

        </div>
        <div id="rpiPins">
                <div class="form" >
                        
                    <h3>Live stream configuration</h3>
                    <br><br>
                    <div>[<br>
                        <div v-for="foot in newProject.pinsLayout" style="margin-left:70px;margin-top:30px;">
                            [<span v-for="(pin,index) in foot" >&emsp;<span v-if='index!==0'>,&emsp;</span><input type="number" :value="pin"></span> &emsp;]
                        </div>]
                    </div>
                </div>
                <img src="https://carinapp.herokuapp.com/public/img/rpi.png" width="400px">

        </div>
  </div>
</template>

<script>
 const axios = require('axios')
axios.defaults.withCredentials = true
    var vueThis
  export default{
    data(){
        return {
            newProject:{
                footPart1S:4,
                footPart2S:5,
                footPart3S:8,
                servoMaxInPWM:2500,
                servoMinInPWM:600,
                high:8,
                highMax:10,
                highMin:5,
                pinsLayout:[
                    [2,3,4],
                    [14,15,18],
                    [22,10,9],
                    [23,24,25],
                    [21,20,16],
                    [26,19,13]
                ]
            }
        }
    },
    methods:{
        send(){
            vueThis = this
            axios.post('https://carinapp.herokuapp.com/api/user/createProject',this.formCardOption.formModels).then(function (response) {
                    console.log(response)
                    if(response.data.status == 'done'){
                        vueThis.$router.push('/user/profile')
                    }else{
                        alert(response.data.error)
                    }
                })
            }
        },
  }
</script>

<style scoped>
body{
    text-align: center;
}
input[type=range]{
    width:300px;
    margin-top:10px;
}
input[type=number]{
    width:40px;
    height:20px;
    background:white;
    border:1px solid black;
    display: inline-block;
    margin-left: 10px;
    font-family:monospace;
    text-align:center;
}
input[type=number]::-webkit-inner-spin-button, 
input[type=number]::-webkit-outer-spin-button { 
  -webkit-appearance: none; 
  margin: 0; 
}
p{
    margin-left: 50px;
    width:300px;
    font-family: 'Courier New', Courier, monospace;
}
.form{
    padding-top: 7px;
    position: absolute;
    background: rgb(14, 201, 248);
    margin: auto;
    top: 0;
    min-height: 400px;
    width: 40%;
    left: 50%;
    border-radius: 30px;
    box-shadow: 0 0 10px #777;
    padding-left:40px;
    font-family: 'Schoolbell', cursive;
}
#footParts{
    position: relative;
    top: 0px;
    right: 0px;   
    width:100%; 
}
#motor{
    position:relative;
    top:100px;
}
#stream{
    position:relative;
    top:200px;
}
#rpiPins{
    position:relative;
    top:300px;
}
h2{
    margin-left: auto;
}
img{
    margin-left:100px;
}
#footParts h3{
    font-family: monospace;
    margin-left: 25%;
}
#motor h3{
    font-family: monospace;
    margin-left: 10%;
}
</style>



