import Vue from 'vue'
import Router from 'vue-router'
import login from '@/components/login'
import regin from '@/components/regin'
import accessMenu from '@/components/accessMenu.vue'
import profile from '@/components/profile.vue'
import userMenu from '@/components/userMenu.vue'
import createProject from '@/components/createProject.vue'
import home from '@/components/home.vue'
import project from '@/components/project.vue'
import controllRobot from '@/components/controllRobot.vue'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/home',
      name: 'home',
      component: home
    },
    {
      path: '/access',
      name: 'accessMenu',
      component: accessMenu,
      children: [
        {
          path: '',
          redirect: 'login'
        },
        {
          path: 'login',
          name: 'login',
          component: login
        },
        {
          path: 'regin',
          name: 'regin',
          component: regin
        },
        {
          path: '*',
          redirect: 'login'
        }
      ]
    },
    {
      path: '/user',
      name: 'user',
      component: userMenu,
      children: [
        {
          path: '',
          redirect: 'profile'
        },
        {
          path: 'profile',
          name: 'profile',
          component: profile
        },
        {
          path: 'project/:id',
          name: 'project',
          component: project
        },
        {
          path: 'controllRobot/:id',
          name: 'controllRobot',
          component: controllRobot
        },
        {
          path: 'createProject',
          name: 'createProject',
          component: createProject
        },
        {
          path: '*',
          redirect: 'profile'
        }
      ]
    },
    {
      path: '*',
      redirect: '/access/login'
    }
  ],
  mode: 'history'
})



