<template>
    <div>
        <div class="ocean">
            <div class="wave"></div>
            <div class="wave"></div>
        </div>
        <table>
         <thead>
              <tr>
                <th>Project name</th>
                <th>Project Id</th>
                <th>Project Status</th>
            </tr>
         </thead>
         <tbody>
            <tr v-for="(project,index) in projects">
                <td>{{ project.projectname }}</td>
                <td>{{ project.id }}</td>
                <td>{{ project.status }}</td>
                <div class="dropdown">
                    <i style="background-color: #3fb0ac;" class="small material-icons dropbtn">more_horiz</i>
                    <div class="dropdown-content">
                        <button v-on:click="goView(index)">View</button>
                        <button v-on:click="goDelete(index)">Delete</button>
                        <a :href="'https://carinapp.herokuapp.com/api/user/downloadProject/'+project.id" download>Download</a>
                        <button v-on:click="goSeeData(index)">See More</button>
                    </div>
                </div> 
                
            </tr>
          </tbody>
       </table>
        <button id="createProjectBtn" class="waves-effect waves-light btn" v-on:click="goToCreateProject">Create project</button>       
    </div>
</template>
<script>

$(document).on('click',$('.dropbtn'),function(e){
    var elem = $(e.target).parent().find('.dropdown-content')
    var opened = elem.hasClass('show')
    $('.dropdown-content').removeClass('show')
    if(!opened) elem.addClass('show') 
})

const axios = require('axios')
axios.defaults.withCredentials = true
    export default{
        data(){
            return { 
                projects: []
            }
        },
        mounted(){
            var vueThis = this
            async function go() {
                try {
                    const wes = await axios('https://carinapp.herokuapp.com/api/user/projects')
                    vueThis.projects = wes.data
                    console.log(wes.data)
                }catch (e) {
                    console.error(e); 
                }
            }
            go()
        },
        methods:{
            goToCreateProject (){
                 this.$router.push('createProject')
            },
            goView (index){
                console.log(index)
                this.$router.push('controllRobot/'+this.projects[index].id)
            },
            goDelete (index){
                var vueThis = this
                axios.delete('https://carinapp.herokuapp.com/api/user/deleteProject/'+this.projects[index].id).then(function (response) {
                    if(response.data.status == 'error') alert(response.data.error)
                    else vueThis.projects.splice(index,1)                 
                })
            },
            goDownload (){
                this.$router.go('https://carinapp.herokuapp.com/controlRobot')
            },
            goSeeData (index) {
                this.$router.push('project/'+this.projects[index].id)
            }
        }
    }    
</script>
<style>
body{
    text-align: center;
}
#createProjectBtn{
    margin:30px;
}
td,th{
    text-align:center;
}
table{
    background:rgba(255,255,255,0.7)
}
i{
    margin-top: 17px !important;
}
th{
    font-family: 'Sigmar One', cursive;
    font-weight: 100;
    font-size: 20px;
}
.dropbtn {
  background-color: #3498DB;
  color: white;
  padding: 5px;
  font-size: 30px;
  border: none;
  cursor: pointer;
  border-radius: 100%;
  margin-bottom: 4px;
  margin-top: 4px;
}

.dropbtn:hover, .dropbtn:focus {
  background-color: #2980B9;
}

.dropdown {
  position: relative;
  display: inline-block;
}

.dropdown-content {
  display: none;
  position: absolute;
  top:40px;
  background-color: #eee;
  min-width: 160px;
  overflow: visible;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  z-index: 1;
  left:-118px;
}

.dropdown-content button ,.dropdown-content a{
  color: #777;
  padding: 12px 16px;
  text-decoration: none;
  background-color: #eee;
  display: block;
  width:100%;
  border:none;
  transition:all .5s;
  text-align: center;
}
tr{
    height: 70px;
}
.dropdown button:hover,.dropdown a:hover {
   color: #fff;
    background-color: #00605B;
}

.show {display: block;opacity:1;}

i:hover{
    background-color: #00605B;
}
table{
    max-width:800px;
    margin:auto;
}
//////////////

html, body { height: 100%; }
body {
  background:radial-gradient(ellipse at center, rgba(255,254,234,1) 0%, rgba(255,254,234,1) 35%, #B7E8EB 100%);
  overflow: hidden;
}

.ocean { 
  height: 5%;
  width:100%;
  position:absolute;
  bottom:0;
  left:0;
  background: #015871;
}

.wave {
  background: url(https://s3-us-west-2.amazonaws.com/s.cdpn.io/85486/wave.svg) repeat-x; 
  position: absolute;
  top: -198px;
  width: 6400px;
  height: 198px;
  animation: wave 7s cubic-bezier( 0.36, 0.45, 0.63, 0.53) infinite;
  transform: translate3d(0, 0, 0);
}

.wave:nth-of-type(2) {
  top: -175px;
  animation: wave 7s cubic-bezier( 0.36, 0.45, 0.63, 0.53) -.125s infinite, swell 7s ease -1.25s infinite;
  opacity: 1;
}

@keyframes wave {
  0% {
    margin-left: 0;
  }
  100% {
    margin-left: -1600px;
  }
}

@keyframes swell {
  0%, 100% {
    transform: translate3d(0,-25px,0);
  }
  50% {
    transform: translate3d(0,5px,0);
  }
}
</style>


