<template>
    <div>
        <!-- <button @click="handleClick">Send message</button> -->
        <img :src="getImageSrc" width="100px" height="100px">
    </div>
</template>
<script>
    import worker1 from '../web-workers/worker'

export default{
    data(){
        return { 
            direction:'',
            socketCopy: null,
            imageSrc: 'data:image/jpeg;base64,',
            imageArray: ['','','','','','','','','','','','','','','','','','','',''],
            socketsID:[],
            workers:[]
        }
    },
    mounted(){
        this.start();
    },
    computed:{
        getImageSrc: function(){
            return this.imageSrc + this.imageArray.join('');
        }
    },
    methods:{
        start: function (){
            const axios = require('axios')
            axios.defaults.withCredentials = true
            var socket = io('https://carinapp.herokuapp.com',{path:'/auth'})
            socket.on('connect',()=>{
                var data = {
                    id: this.$route.params.id
                }
                axios.get('https://carinapp.herokuapp.com/api/user/getCookies').then( (response) => {
                    data.typeAccess = response.data.typeAccess
                    data.token = response.data.token
                    socket.emit('verifyUser',data)
                    socket.on('verifyUser_res',(res)=>{
                        if(res.status=='error'){
                            this.verifyError = res.error
                            console.error('Error messgae :: ',res.error)
                            return;
                        }
                        socket.robot_id = res.id;
                        this.startControll()
                        this.startStream();
                        this.socketCopy = socket;
                    })
                })
            })
        },
        startStream: function () {
            for(let i=0; i<20; i++){
                this.workers[i] = worker1.worker('https://carinapp.herokuapp.com',i)
                this.workers[i].onmessage = (msg)=>{
                    if(msg.data.message_name == 'setID'){
                        this.socketsID.push(msg.data.data)
                        if(this.socketsID.length == 20){
                            // console.log()
                            this.socketCopy.emit('setUserStreamIds',this.socketsID)
                        }
                    }else{
                        this.imageArray[i] = msg.data;
                        this.imageArray = Object.assign([],this.imageArray)
                    }
                }
                this.workers[i].postMessage('start');
            }
        },
        startControll: function () {

            var sampleJoystick = {
                mode: 'static',
                position: {
                left: '120px',
                bottom: '120px'
                },
                size: 200,
                color: 'red'
            };

            var joystick = nipplejs.create(sampleJoystick);
            joystick.on('start end', (evt, data) => {
                if(evt.type=='end'){
                    this.socketCopy.emit('setDirection', {direction: 'stop'})
                    this.direction = 'stop'
                }
            }).on('move', (evt, data) => {
                if(data.direction == undefined) return
                if(data.direction.angle !== this.direction) {
                    this.socketCopy.emit('setDirection', {direction: data.direction.angle})
                    this.direction = data.direction.angle
                }
            }).on('dir:up plain:up dir:left plain:left dir:down'+'plain:down dir:right plain:right',(evt, data)=>{
            }).on('pressure', (evt, data) => {
            });
        }
}
}
</script>
<style>

</style>


