<template>
    <div>
        <!-- <code>{{project}}</code> -->
        <div class='json-style'>
            <code class="head">projectData :</code>
            <div class="content">
                <div v-for="(property,index) in project.projectdata">
                    <div>
                        <code>{{property.name}} :</code> <textarea :readonly='!edit' v-model='project.projectdata[index].model'></textarea>
                    </div>
                </div>
            </div>
            <a @click='edit=true' class="waves-effect waves-teal btn-flat" v-if='!edit'>Edit</a>
            <a @click='edit=false' class="waves-effect waves-teal btn-flat" v-if='edit'>Cancel</a>
            <a @click='updateProject()' class="waves-effect waves-teal btn-flat" v-if='edit'>Update</a>
        </div>
    </div>
</template>
<script>

const axios = require('axios')
axios.defaults.withCredentials = true
    export default{
        data(){
            return { 
                project: [],
                edit:false
            }
        },
        mounted(){
            console.log()
            var vueThis = this
            async function go() {
                try {
                    const wes = await axios('https://carinapp.herokuapp.com/api/user/project/'+this.$route.id)
                    vueThis.project = wes.data[0]
                    console.log(wes)
                }catch (e) {
                    console.error(e); 
                }
            }
            go()
        },
        methods:{
            updateProject (){
                var vueThis = this
                axios.put('https://carinapp.herokuapp.com/api/user/editProject',vueThis.project).then(function (response) {
                    if(response.data.status == 'error') alert(response.data.error)
                    else vueThis.edit = false
                })
            }
        }
    }    
</script>
<style>
    .json-style::-webkit-scrollbar {
        width: 1em;
        background: transparent;
        visibility: hidden;
        display: none;
    }

    .json-style:hover::-webkit-scrollbar {
        display: block;
    }    
    .json-style::-webkit-scrollbar-track {
        -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
        visibility: hidden;    
    }
    
    .json-style::-webkit-scrollbar-thumb {
        background-color: darkgrey;
        outline: 1px solid slategrey;
    }
    .json-style{
        width: 470px;
        padding: 14px;
        max-height: 100%;
        overflow-y: scroll;
        background: #eee;
        border-radius: 10px;
        box-shadow: 0 0 10px #aaa;
        margin-left: 100px;
    }

    .json-style .head{
        font-size:20px;
        margin-left: 10px;
    }

    .json-style .content{
        margin-left: 70px;
    }

    textarea{
        width: 200px;
        resize: none;
        outline: none;
        border-radius: 10px;
        padding: 8px;
        text-align: center;
        vertical-align: middle;
        font-size: 15px;
        font-family: monospace;
        margin:10px;
        background:#fefefe;
    }
    textarea:read-only{
        background: transparent;
    }
    .json-style a{
        background:#ddd !important;
    }
</style>


