export default {
	workerOne : null,
	id1: null,
	path: '',
	host: 'http://127.0.0.1',
	worker(host, path){
		this.host = host;
		this.path = path;
		var workerBlob = new Blob([this.workerRunner()],{type: 'text/javascript'});
		var workerBlobUrl = URL.createObjectURL(workerBlob);
		this.worker1 = new Worker(workerBlobUrl);
		return this.worker1;
	},
	workerRunner() {
		return `
		importScripts('https://carinapp.herokuapp.com/socket.io/socket.io.js');
		var socket = io('${this.host}',{path:'/stream${this.path}'});

		socket.on('connect', ()=>{
			this.id1 = socket.io.engine.id;
			self.postMessage({ message_name : 'setID',data : { 
				id : socket.io.engine.id,
				index : ${this.path}
			} });
			socket.on('stream',(data)=>{
				self.postMessage(data)
			})
		});
		self.onmessage = function(msg) { 
			if(msg.message_name == 'start_stream') {
				socket.robotId = msg.id
				socket.on('stream', (data)=>{
					self.postMessage(data);
				})
			}
		}`
		
	}
}


